import React from 'react';
import Chart from '@/component/Chart/chart'

type CardProps = {
  name: string;
  description: string;
  Buttonlink: string;
};

const Card = ({ name, description,Buttonlink }: CardProps) => {
  return (
    <div className='row flex'>
    <div className="col-6 p-4 rounded shadow bg-white w-full">
      <h3 className="text-lg font-semibold text-black">{name}</h3>
      <p className="text-sm text-black mt-2">{description}</p>
      <button className="mt-4 px-4 py-2 bg-blue-500 text-white rounded">{Buttonlink}</button>
    </div>
    <div className="col-6 p-4 rounded shadow bg-white w-full">
      <Chart />
    </div>
  </div>
  
    
  );
};

export default Card;